.toast-title {
  font-weight: bold;
}

.toast-message {
  -ms-word-wrap: break-word;
  word-wrap: break-word;
}

.toast-message a,
.toast-message label {
  color: $white;
}

.toast-message a:hover {
  color: #cccccc;
  text-decoration: none;
}

.toast-close-button {
  position: relative;
  right: -0.3em;
  top: -0.3em;
  float: right;
  font-weight: bold;
  color: $white;
  opacity: 0.8;
}

.toast-close-button:hover,
.toast-close-button:focus {
  color: $black;
  text-decoration: none;
  cursor: pointer;
  opacity: 0.4;
}

button.toast-close-button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: 0;
  -webkit-appearance: none;
}

.toast-top-center {
  top: 0;
  right: 0;
  width: 100%;
}

.toast-bottom-center {
  bottom: 0;
  right: 0;
  width: 100%;
}

.toast-top-full-width {
  top: 0;
  right: 0;
  width: 100%;

  .ngx-toastr {
    @include border-top-radius(0);
  }
}

.toast-bottom-full-width {
  bottom: 0;
  right: 0;
  width: 100%;

  .ngx-toastr {
    @include border-bottom-radius(0);
  }
}

.toast-top-left {
  top: 12px;
  left: 12px;
}

.toast-top-right {
  top: 12px;
  right: 12px;
}

.toast-bottom-right {
  right: 12px;
  bottom: 12px;
}

.toast-bottom-left {
  bottom: 12px;
  left: 12px;
}

#toast-container {
  position: fixed;
  z-index: 999999;
  /*overrides*/

}

#toast-container * {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

#toast-container > div {
  position: relative;
  overflow: hidden;
  margin: 0 0 .6rem;
  padding: .6rem .6rem .6rem 50px;
  width: 300px;
  @include border-radius($border-radius);
  background-position: 15px center;
  background-repeat: no-repeat;
  box-shadow: $box-shadow-default;
  color: $white;
  opacity: 0.9;
}

#toast-container > div:hover {
  opacity: 1;
  cursor: pointer;
}

#toast-container > .toast-info {
  background-image: url("data:image/png;base64,") !important;
  @include box-shadow-architect($info);
}

#toast-container > .toast-error {
  background-image: url("data:image/png;base64,") !important;
  @include box-shadow-architect($danger);
}

#toast-container > .toast-success {
  background-image: url("data:image/png;base64,") !important;
  @include box-shadow-architect($success);
}

#toast-container > .toast-warning {
  background-image: url("data:image/png;base64,") !important;
  @include box-shadow-architect($warning);
  color: color-contrast($warning);
}

#toast-container.toast-top-center > div,
#toast-container.toast-bottom-center > div {
  width: 300px;
  margin-left: auto;
  margin-right: auto;
}

#toast-container.toast-top-full-width > div,
#toast-container.toast-bottom-full-width > div {
  width: 96%;
  margin-left: auto;
  margin-right: auto;
}

.toast {
  background-color: $dark;
}

.toast-success {
  background-color: $success;
}

.toast-error {
  background-color: $danger;
}

.toast-info {
  background-color: $info;
}

.toast-warning {
  background-color: $warning;
}

.toast-progress {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 4px;
  background-color: $black;
  opacity: 0.4;
}

@media all and (max-width: 240px) {
  #toast-container > div {
    padding: 8px 8px 8px 50px;
    width: 11em;
  }
  #toast-container .toast-close-button {
    right: -0.2em;
    top: -0.2em;
  }
}

@media all and (min-width: 241px) and (max-width: 480px) {
  #toast-container > div {
    padding: 8px 8px 8px 50px;
    width: 18em;
  }
  #toast-container .toast-close-button {
    right: -0.2em;
    top: -0.2em;
  }
}

@media all and (min-width: 481px) and (max-width: 768px) {
  #toast-container > div {
    padding: 15px 15px 15px 50px;
    width: 25em;
  }
}
