    #fe9a8b 100%
  ) !important;
}

.bg-grow-early {
  background-image: linear-gradient(
    to top,
    #0ba360 0%,
    #3cba92 100%
  ) !important;
}

.bg-love-kiss {
  background-image: linear-gradient(
    to top,
    #ff0844 0%,
    #ffb199 100%
  ) !important;
}

.bg-premium-dark {
  background-image: linear-gradient(
    to right,
    #434343 0%,
    black 100%
  ) !important;
}

.bg-happy-green {
  background-image: linear-gradient(to bottom, #00b09b, #96c93d) !important;
}

.bg-vicious-stance {
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%) !important;
}

.bg-midnight-bloom {
  background-image: linear-gradient(
    -20deg,
    #2b5876 0%,
    #4e4376 100%
  ) !important;
}

.bg-blue-secure-pichincha {
  background-color: #001689 !important;
}


.bg-plum-secure {
  background-image: linear-gradient(
    135deg,
    #1631b4 0%,
    #001689 100%
  ) !important;
}

.bg-night-sky {
  background-image: linear-gradient(
    to top,
    #1e3c72 0%,
    #1e3c72 1%,
    #2a5298 100%
  ) !important;
}

.bg-slick-carbon {
  background-image: linear-gradient(
      to bottom,
      #323232 0%,
      #3f3f3f 40%,
      #1c1c1c 150%
    ),
    linear-gradient(
      to top,
      rgba(255, 255, 255, 0.4) 0%,
      rgba(0, 0, 0, 0.25) 200%
    ) !important;
  background-blend-mode: multiply;
}

.bg-royal {
  background-image: linear-gradient(to right, #141e30, #243b55) !important;
}

.bg-asteroid {
  background-image: linear-gradient(
    to right,
    #0f2027,
    #203a43,
    #2c5364
  ) !important;
}

.bg-transparent {
  background: transparent !important;
}

